@import './base.scss';

.cardsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    height: 209px;
    width: 100%;
    padding: 40px 0;
    overflow-x: hidden;

    @media screen and (max-width: 500px) {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #000;
        width: 100%;
        overflow-y: auto;
        padding-top: 16px;
    }
}

.card {
    width: 150px;
    height: 210px;
    position: relative;
    transition: transform 0.3s;
    z-index: 1;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow-x: hidden;
    margin-bottom: -150px;

    @media screen and (max-width: 500px) {
        position: relative;
        transition: transform 0.3s;
        z-index: 1;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        overflow: hidden;
    }

    &:last-child {
        margin-bottom: 0; /* Reset margin for the last card to align it at the bottom without overlap */
    }
}

.cardImage {
    width: auto;
    height: 100%;
    border-radius: 8px;
    position: absolute;
    top: 0;
    left: 0;

    @media screen and (max-width: 500px) {
        width: 100%;
        height: auto;
        border-radius: 8px;
    }
}